<template>
  <div class="profile-page">
    <!-- 顶部导航栏 -->
    <!-- <div class="profile-header">
      <el-button 
        icon="el-icon-arrow-left" 
        class="back-btn"
        @click="$router.back()"
      ></el-button>
      <h1 class="page-title">任务中心</h1>
    </div> -->

    <!-- 用户信息卡片 -->
    <div class="user-card">
      <div class="avatar-container">
        <!-- <el-avatar 
          :size="80" 
          class="user-avatar"
        >
          <img :src="userAvatar" alt="用户头像">
        </el-avatar> -->
      </div>
      <div class="user-info">
        <h2 class="username">{{ userName }}</h2>
        <p class="user-email">{{ userEmail }}</p>
        <el-button 
          type="text" 
          class="edit-btn"
          @click="showEditDialog = true"
        >
          编辑资料
        </el-button>
      </div>
    </div>

    <!-- 任务统计卡片 -->
    <div class="stats-grid">
      <div class="stat-item">
        <p class="stat-value">{{ totalTasks }}</p>
        <p class="stat-label">总任务数</p>
      </div>
      <div class="stat-item">
        <p class="stat-value">{{ completedTasks }}</p>
        <p class="stat-label">已完成</p>
      </div>
      <div class="stat-item">
        <p class="stat-value">{{ processingTasks }}</p>
        <p class="stat-label">处理中</p>
      </div>
    </div>

    <!-- 最近任务列表（el-table 替换 el-list） -->
    <div class="section-title">最近任务</div>
    <el-card class="task-list-card">
      <el-empty 
        v-if="recentTasks.length === 0"
        description="暂无任务记录"
      ></el-empty>
      
      <!-- 任务表格 -->
      <el-table 
        v-else
        :data="recentTasks"
        class="task-table"
        border
        stripe
        :header-cell-style="{ background: '#f5f7fa', fontSize: '14px', padding: '10px 0' }"
        :cell-style="{ padding: '12px 8px' }"
      >
        <!-- 任务名称列 -->
        <el-table-column 
          prop="name" 
          label="任务名称" 
          align="left"
          width="220"
        >
          <template #default="scope">
            <span class="task-name">{{ scope.row.name }}</span>
          </template>
        </el-table-column>

        <!-- 创建时间列 -->
        <el-table-column 
          prop="createTime" 
          label="创建时间" 
          align="center"
          width="180"
        >
          <template #default="scope">
            <span class="task-time">{{ formatTime(scope.row.createTime) }}</span>
          </template>
        </el-table-column>

        <!-- 任务状态列 -->
        <el-table-column 
          prop="status" 
          label="状态" 
          align="center"
          width="120"
        >
          <template #default="scope">
            <el-tag 
              :type="scope.row.status === 'SUCCESS' ? 'success' : scope.row.status === 'RUNNING' ? 'info' : 'danger'"
              class="task-status"
            >
              {{ scope.row.status === 'SUCCESS' ? '已完成' : scope.row.status === 'RUNNING' ? '处理中' : '已失败' }}
            </el-tag>
          </template>
        </el-table-column>

        <!-- 操作列 -->
        <!-- <el-table-column 
          label="操作" 
          align="center"
          width="100"
        >
          <template #default="scope">
            <el-button 
              type="text" 
              @click="goToTaskDetail(scope.row.id)"
              class="view-btn"
            >
              查看
            </el-button>
          </template>
        </el-table-column> -->
      </el-table>
    </el-card>

    <!-- 底部操作按钮 -->
    <div class="action-buttons">
      <el-button 
        type="primary" 
        class="primary-btn"
        @click="goToCreateTask"
      >
        切换用户
      </el-button>
      <el-button 
        type="warning" 
        class="logout-btn"
        @click="handleLogout"
      >
        退出登录
      </el-button>
    </div>

    <!-- 编辑资料弹窗 -->
    <el-dialog 
      title="编辑资料" 
      v-model="showEditDialog"
      width="80%"
    >
      <el-form 
        ref="editForm"
        :model="editForm"
        :rules="editRules"
        label-width="80px"
      >
        <el-form-item label="用户名" prop="username">
          <el-input v-model="editForm.username"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="editForm.email" type="email"></el-input>
        </el-form-item>
        <!-- <el-form-item label="头像">
          <el-upload
            class="avatar-uploader"
            action=""
            :show-file-list="false"
            :on-change="handleAvatarChange"
            accept="image/*"
          >
            <img 
              v-if="editForm.avatar" 
              :src="editForm.avatar" 
              class="avatar-preview"
            >
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item> -->
      </el-form>
      <template #footer>
        <el-button @click="showEditDialog = false">取消</el-button>
        <el-button type="primary" @click="saveUserInfo">保存</el-button>
      </template>
    </el-dialog>

    <!-- 设置弹窗 -->
    <el-dialog 
      title="设置" 
      v-model="showSettingDialog"
      width="80%"
    >
      <el-switch 
        v-model="notificationEnabled" 
        active-text="通知已开启" 
        inactive-text="通知已关闭"
        class="setting-item"
      ></el-switch>
      <el-switch 
        v-model="autoSaveEnabled" 
        active-text="自动保存已开启" 
        inactive-text="自动保存已关闭"
        class="setting-item"
      ></el-switch>
      <el-button 
        type="text" 
        class="version-info"
      >
        当前版本: v1.0.0
      </el-button>
      <template #footer>
        <el-button @click="showSettingDialog = false">确定</el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import axios from 'axios';
const tasks =  [
  {
    id: 1001,
    name: "客厅场景3D重建",
    createTime: "2023-10-21T14:30:25", //  ISO时间格式，可通过formatTime转换
    status: "SUCCESS", // 状态：已完成
    // 可选：添加任务时长、文件大小等扩展信息
    duration: "15分钟",
    fileSize: "18.5MB"
  },
  {
    id: 1002,
    name: "户外建筑扫描处理",
    createTime: "2023-10-21T11:15:40",
    status: "RUNNING", // 状态：处理中
    duration: "8分钟（进行中）",
    progress: 68 // 进度百分比（处理中任务可添加）
  },
  {
    id: 1003,
    name: "产品模型生成",
    createTime: "2023-10-20T09:50:12",
    status: "FAILED", // 状态：已失败
    duration: "5分钟",
    error: "视频清晰度不足，无法提取特征点" // 失败原因
  },
  {
    id: 1004,
    name: "卧室全景建模",
    createTime: "2023-10-20T16:22:30",
    status: "SUCCESS",
    duration: "12分钟",
    fileSize: "12.3MB"
  }
];
export default {
  data() {
    return {
      // 用户信息
      userName: '小明',
      userEmail: 'xxxxxxx@xxx.com',
    //   userAvatar: require('@/assets/default-avatar.png'),
      
      // 任务统计
      totalTasks: 0,
      completedTasks: 0,
      processingTasks: 0,
      
      // 最近任务列表
      recentTasks: tasks,
      
      // 弹窗控制
      showEditDialog: false,
      showSettingDialog: false,
      
      // 编辑表单数据
      editForm: {
        username: '',
        email: '',
        avatar: ''
      },
      
      // 编辑表单验证规则
      editRules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 3, max: 16, message: '用户名长度为3-16位', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '请输入邮箱', trigger: 'blur' },
          { type: 'email', message: '请输入正确的邮箱格式', trigger: 'blur' }
        ]
      },
      
      // 设置项
      notificationEnabled: true,
      autoSaveEnabled: true
    };
  },
  
  methods: {
    // 格式化时间
    formatTime(time) {
      const date = new Date(time);
      return date.toLocaleString();
    },
    
    // 前往任务详情
    goToTaskDetail(taskId) {
      this.$router.push(`/task-detail?id=${taskId}`);
    },
    
    // 前往新建任务
    goToCreateTask() {
      this.$router.push('/task');
    },
    
    // 退出登录
    handleLogout() {
      this.$confirm('确定要退出登录吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        // 清除本地存储
        localStorage.removeItem('token');
        localStorage.removeItem('username');
        // 跳转到登录页
        this.$router.push('/login');
        this.$message.success('退出登录成功');
      }).catch(() => {
        // 取消退出
      });
    },
    
    // 处理头像上传
    handleAvatarChange(file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        this.editForm.avatar = e.target.result;
      };
      reader.readAsDataURL(file.raw);
    },
    
    // 保存用户信息
    async saveUserInfo() {
      const valid = await this.$refs.editForm.validate();
      if (!valid) return;
      
      try {
        // 模拟接口请求
        await axios.post('/api/user/update', this.editForm);
        // 更新页面显示
        this.userName = this.editForm.username;
        this.userEmail = this.editForm.email;
        this.userAvatar = this.editForm.avatar;
        this.showEditDialog = false;
        this.$message.success('资料更新成功');
      } catch (error) {
        this.$message.error('资料更新失败，请重试');
      }
    },
    
    // 获取用户信息
    async getUserInfo() {
      try {
        const res = await axios.get('/api/user/info');
        const data = res.data.data;
        this.userName = data.username;
        this.userEmail = data.email;
        // this.userAvatar = data.avatar || require('@/assets/default-avatar.png');
        
        // 初始化编辑表单
        this.editForm.username = data.username;
        this.editForm.email = data.email;
        // this.editForm.avatar = data.avatar || require('@/assets/default-avatar.png');
      } catch (error) {
        console.error('获取用户信息失败', error);
      }
    },
    
    // 获取任务统计
    async getTaskStats() {
      try {
        const res = await axios.get('/api/user/task-stats');
        const data = res.data.data;
        this.totalTasks = data.total;
        this.completedTasks = data.completed;
        this.processingTasks = data.processing;
      } catch (error) {
        console.error('获取任务统计失败', error);
      }
    },
    
    // 获取最近任务
    async getRecentTasks() {
      try {
        const res = await axios.get('/api/user/recent-tasks');
        this.recentTasks = res.data.data || [];
      } catch (error) {
        console.error('获取最近任务失败', error);
      }
    }
  },
  
  mounted() {
    window.scrollTo({
        top: 0,
        behavior: 'smooth' // 可选：添加平滑滚动效果
    });
    // 页面加载时获取数据
    // this.getUserInfo();
    // this.getTaskStats();
    // this.getRecentTasks();
  }
};
</script>

<style scoped>
.profile-page {
  min-height: 100vh;
  background-color: #f8f9fa;
  padding-bottom: 20px;
}

/* 顶部导航 */
.profile-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 15px;
  background-color: #fff;
  border-bottom: 1px solid #e0e0e0;
}

.back-btn, .setting-btn {
  width: 40px;
  height: 40px;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.page-title {
  font-size: 18px;
  font-weight: 500;
  color: #333;
  margin: 0;
}

/* 用户信息卡片 */
.user-card {
  display: flex;
  align-items: center;
  padding: 20px 15px;
  background-color: #fff;
  margin-bottom: 15px;
}

.avatar-container {
  margin-right: 15px;
}

.user-avatar {
  border: 2px solid #f0f0f0;
}

.user-info {
  flex: 1;
}

.username {
  font-size: 18px;
  font-weight: 500;
  margin: 0 0 5px 0;
  color: #333;
}

.user-email {
  font-size: 14px;
  color: #666;
  margin: 0 0 10px 0;
}

.edit-btn {
  padding: 0;
  color: #409eff;
}

/* 统计卡片 */
.stats-grid {
  display: flex;
  padding: 0 15px;
  margin-bottom: 15px;
  gap: 10px;
}

.stat-item {
  flex: 1;
  background-color: #fff;
  border-radius: 8px;
  padding: 15px 10px;
  text-align: center;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.03);
}

.stat-value {
  font-size: 22px;
  font-weight: bold;
  color: #333;
  margin: 0 0 5px 0;
}

.stat-label {
  font-size: 14px;
  color: #666;
  margin: 0;
}

/* 任务列表（表格样式） */
.section-title {
  font-size: 16px;
  font-weight: 500;
  color: #333;
  padding: 0 15px 10px;
  margin: 15px 0 5px;
}

.task-list-card {
  margin: 0 15px;
  border-radius: 8px;
  border: 1px solid #e0e0e0;
  overflow: hidden;
}

/* 表格核心样式 */
.task-table {
  width: 100%;
  background-color: #fff;
  font-size: 14px;
}

.task-name {
  color: #333;
  word-break: break-all; /* 长任务名换行 */
}

.task-time {
  font-size: 13px;
  color: #666;
}

.task-status {
  font-size: 12px;
  padding: 3px 8px;
}

.view-btn {
  color: #409eff;
  padding: 5px 10px;
  font-size: 13px;
}

/* 底部按钮 */
.action-buttons {
  display: flex;
  gap: 10px;
  padding: 15px;
}

.primary-btn, .logout-btn {
  flex: 1;
  height: 46px;
  font-size: 16px;
  border-radius: 6px;
}

/* 编辑资料弹窗 */
.avatar-uploader {
  display: flex;
  align-items: center;
  justify-content: center;
}

.avatar-preview {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  object-fit: cover;
}

.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 100px;
  height: 100px;
  line-height: 100px;
  text-align: center;
  border: 1px dashed #d9d9d9;
  border-radius: 50%;
}

/* 设置弹窗 */
.setting-item {
  display: block;
  margin-bottom: 15px;
  padding: 10px 0;
  border-bottom: 1px solid #f5f5f5;
}

.version-info {
  display: block;
  text-align: center;
  margin-top: 20px;
  color: #999;
}

/* 安卓端适配：小屏幕横向滚动 */
@media (max-width: 768px) {
  .user-card {
    padding: 15px;
  }
  
  .stat-item {
    padding: 12px 5px;
  }
  
  .stat-value {
    font-size: 20px;
  }
  
  /* 表格横向滚动 */
  .task-list-card {
    overflow-x: auto;
    -webkit-overflow-scrolling: touch; /* 平滑滚动 */
  }
  
  .task-table {
    min-width: 500px; /* 最小宽度，确保列不挤压 */
  }
}
</style>