<template>
  <div class="render-task-container">
    <!-- 顶部导航栏 -->
    <div class="navbar">
      <h1 class="nav-title">模型渲染视图</h1>
      <el-button 
        class="nav-btn help-btn"
        size="default"
        @click="loadModelFromFlutter"
        circle
      >
        <el-icon><Upload /></el-icon>
      </el-button>
    </div>

    <!-- 中间核心区：3D渲染容器 -->
    <div class="core-content">
      <div  
        ref="viewerContainer" 
        class="viewer-container" 
        :class="{ 'fullscreen': isManualFullscreen }"
        @dblclick="isManualFullscreen = !isManualFullscreen"
        @touchstart="handleTouchStart"
        @click="closeControlPanel"
      >
        <!-- 空状态提示 -->
        <div v-if="!splatScene && !isViewerLoading" class="empty-state">
          <div class="empty-icon">
            <i class="el-icon-cube"></i>
          </div>
          <p class="empty-text">尚未加载模型</p>
          <p class="empty-subtext">点击下方按钮导入3D模型文件</p>
        </div>
      </div>
    </div>

    <!-- 可拖动悬浮控制区 -->
    <div 
      class="draggable-float-container"
      :style="{ left: floatLeft + 'px', top: floatTop + 'px' }"
      @mousedown="startDrag"
      @touchstart="startDrag"
      @mouseup="stopDrag"
      @touchend="stopDrag"
      @mousemove="handleDrag"
      @touchmove="handleDrag"
    >
      <!-- 悬浮球 -->
      <div 
        class="control-float-btn"
        @click.stop="toggleControlPanel"
        :class="{ 'active': isControlPanelOpen }"
      >
        <i class="el-icon-dashboard"></i>
      </div>

      <!-- 控制按钮组 -->
      <div class="control-circle-container" :class="{ 'show': isControlPanelOpen }">
        <!-- 1. 显示/隐藏信息 -->
        <div class="control-btn-item pos-1">
          <el-tooltip effect="dark" content="显示/隐藏信息" placement="top">
            <el-button 
              :type="viewerControls.showInfo ? 'primary' : 'default'"
              @click.stop="toggleInfoPanel" 
              size="default" 
              circle
            >
            <el-icon><Search /></el-icon>
              
            </el-button>
          </el-tooltip>
        </div>

        <!-- 2. 显示/隐藏光标 -->
        <div class="control-btn-item pos-2">
          <el-tooltip effect="dark" content="显示/隐藏光标" placement="top">
            <el-button 
              :type="viewerControls.showMeshCursor ? 'primary' : 'default'"
              @click.stop="toggleMeshCursor" 
              size="default" 
              circle
            >
              <el-icon><WindPower /></el-icon>
            </el-button>
          </el-tooltip>
        </div>

        <!-- 3. 显示/隐藏平面 -->
        <div class="control-btn-item pos-3">
          <el-tooltip effect="dark" content="显示/隐藏平面" placement="top">
            <el-button 
              :type="viewerControls.showControlPlane ? 'primary' : 'default'"
              @click.stop="toggleControlPlane" 
              size="default" 
              circle
            >
              <el-icon><Place /></el-icon>
            </el-button>
          </el-tooltip>
        </div>

        <!-- 4. 切换透视/正交 -->
        <div class="control-btn-item pos-4">
          <el-tooltip effect="dark" content="切换透视/正交" placement="top">
            <el-button 
              :type="viewerControls.isOrthographicCamera ? 'default' : 'primary'"
              @click.stop="toggleCameraProjectionMode" 
              size="default" 
              :disabled="viewerControls.usingExternalCamera"
              circle
            >
              <el-icon><Camera /></el-icon>
            </el-button>
          </el-tooltip>
        </div>

        <!-- 5. 点云模式 -->
        <div class="control-btn-item pos-5">
          <el-tooltip effect="dark" content="切换点云模式" placement="top">
            <el-button 
              :type="viewerControls.isPointCloudMode ? 'primary' : 'default'"
              @click.stop="togglePointCloudMode" 
              size="default" 
              :disabled="viewerControls.usingExternalCamera"
              circle
            >
              <el-icon><MostlyCloudy /></el-icon>
            </el-button>
          </el-tooltip>
        </div>
        
        <!-- 6. 焦距加 -->
        <div class="control-btn-item pos-6">
          <el-tooltip effect="dark" content="焦距+" placement="top">
            <el-button 
              @click.stop="adjustFocalUp" 
              size="default" 
              circle
            >
              <img src="focusPlus.svg" alt="" style="width: 20px;height: 20px;">
            </el-button>
          </el-tooltip>
        </div>
        
        <!-- 7. 焦距减 -->
        <div class="control-btn-item pos-7">
          <el-tooltip effect="dark" content="焦距-" placement="top">
            <el-button 
              @click.stop="adjustFocalDown" 
              size="default" 
              circle
            >
              <img src="focusMinus.svg" alt="" style="width: 20px;height: 20px;">
            </el-button>
          </el-tooltip>
        </div>
        
        <!-- 8. 点+ -->
        <div class="control-btn-item pos-8">
          <el-tooltip effect="dark" content="点+" placement="top">
            <el-button 
              @click.stop="increaseSplatScale" 
              size="default" 
              :disabled="viewerControls.usingExternalCamera"
              circle
            >
              <img src="Plus.svg" alt="" style="width: 20px;height: 20px;">
            </el-button>
          </el-tooltip>
        </div>
        
        <!-- 9. 点- -->
        <div class="control-btn-item pos-9">
          <el-tooltip effect="dark" content="点-" placement="top">
            <el-button 
              @click.stop="decreaseSplatScale" 
              size="default" 
              :disabled="viewerControls.usingExternalCamera"
              circle
            >
              <img src="Minus.svg" alt="" style="width: 20px;height: 20px;">
            </el-button>
          </el-tooltip>
        </div>

        <!-- 10. 相机上移 -->
        <div class="control-btn-item pos-10">
          <el-tooltip effect="dark" content="相机上移" placement="top">
            <el-button 
              @click.stop="handleCameraClick('up')"
              @touchstart.stop="startLongPress('up')"
              @touchend.stop="stopLongPress()"
              @touchmove.stop="stopLongPress()"
              size="default" 
              :disabled="viewerControls.usingExternalCamera"
              circle
            >
              <img src="cameraUp.svg" alt="" style="width: 20px;height: 20px;">
            </el-button>
          </el-tooltip>
        </div>

        <!-- 11. 相机下移 -->
        <div class="control-btn-item pos-11">
          <el-tooltip effect="dark" content="相机下移" placement="top">
            <el-button 
              @click.stop="handleCameraClick('down')"
              @touchstart.stop="startLongPress('down')"
              @touchend.stop="stopLongPress()"
              @touchmove.stop="stopLongPress()"
              size="default" 
              :disabled="viewerControls.usingExternalCamera"
              circle
            >
              <img src="cameraDown.svg" alt="" style="width: 20px;height: 20px;">
            </el-button>
          </el-tooltip>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as GaussianSplats3D from '@mkkellogg/gaussian-splats-3d';
import { ElMessage, ElTooltip } from 'element-plus';
import { Search, WindPower, Place, Camera, MostlyCloudy } from '@element-plus/icons-vue';

let viewer = null;
export default {
  components: { Search, WindPower, Place, Camera, MostlyCloudy, ElTooltip },
  props:{
    isView: {
      type: Boolean,
      required: true,
    },
    viewData:{
      type: ArrayBuffer,
      default: null,
    },
    viewFileName:{
      type: String,
      default: null,
    }
  },
  data() {
    return {
      isManualFullscreen: false,
      splatScene: false,
      isViewerLoading: true,
      // 触摸事件
      touchStartTime: 0,
      touchCount: 0,
      touchTimeout: null,
      totalDragX: 0,
      totalDragY: 0,
      dragThreshold: 5,
      // 控制悬浮球
      isControlPanelOpen: false,
      viewerControls: {
        showInfo: false,               
        showMeshCursor: true,         
        showControlPlane: false,       
        focalAdjustment: 0,            
        isOrthographicCamera: true,
        isPointCloudMode: false,
        usingExternalCamera: false,    
      },
      // 悬浮球拖拽相关
      isDragging: false,
      floatLeft: 800, // 初始位置（右侧）
      floatTop: 400,  // 初始位置（中间）
      startX: 0,
      startY: 0,
      // 屏幕边界限制
      screenPadding: 20, // 距离屏幕边缘的最小距离
      // 长按相关
      longPressTimer: null,
      currentPressDir: null,
      // 发光状态
      isGlowing: {
        up: false,
        down: false
      },
      glowTimer: null
    };
  },
  methods: {
    initViewer() {
      const container = this.$refs.viewerContainer;
      if (!container) return;

      const viewerOptions = {
        rootElement: container,
        cameraUp: [0, -1, -0.6],
        initialCameraPosition: [0, 1, 5],
        initialCameraLookAt: [0, 0, 0],
        antialiased: false,
        sphericalHarmonicsDegree: 0,
        gpuAcceleratedSort: false,
        sharedMemoryForWorkers: false
      };
    
      try {
        viewer = new GaussianSplats3D.Viewer(viewerOptions);
        this.isViewerLoading = false;
        ElMessage.success('渲染环境准备就绪');
      } catch (error) {
        this.isViewerLoading = false;
        ElMessage.error('初始化渲染器失败：' + error.message);
      }
    },

    // 重置相机位置
    resetCamera() {
      if (!viewer) return;
      viewer.resetCamera();
      ElMessage.info('相机已重置');
    },

    loadModelFromFlutter() {
      ElMessage.info('请求读取模型...');
      this.isViewerLoading = true;

      if (window.FlutterFileChannel) {
        window.FlutterFileChannel.postMessage('readFile');
      } else {
        const fileInput = document.createElement('input');
        fileInput.type = 'file';
        fileInput.accept = '.ply,.splat,.ksplat';
        fileInput.title = '请选择.ply、.splat或.ksplat格式的模型文件';

        fileInput.addEventListener('change', (e) => {
          const file = e.target.files[0];
          if (!file) {
            this.isViewerLoading = false;
            ElMessage.error('未选择任何文件');
            return;
          }

          const validExtensions = ['ply', 'splat', 'ksplat'];
          const fileExtension = file.name.split('.').pop().toLowerCase();
          if (!validExtensions.includes(fileExtension)) {
            this.isViewerLoading = false;
            ElMessage.error('请选择.ply、.splat或.ksplat格式的模型文件');
            return;
          }

          const reader = new FileReader();
          reader.onload = (event) => {
            const base64Data = event.target.result.split(',')[1];
            this.renderFromBase64(file.name, base64Data);
          };
          reader.onerror = () => {
            this.isViewerLoading = false;
            ElMessage.error('文件读取失败，请重试');
          };
          reader.readAsDataURL(file);
        });

        fileInput.click();
      }
    },

    renderFromBase64(fileName, base64Data) {
      try {
        const binaryStr = atob(base64Data);
        const arrayBuffer = new ArrayBuffer(binaryStr.length);
        const uint8Array = new Uint8Array(arrayBuffer);
        
        for (let i = 0; i < binaryStr.length; i++) {
          uint8Array[i] = binaryStr.charCodeAt(i);
        }

        this.renderFromArrayBuffer(fileName, arrayBuffer);
      } catch (error) {
        this.isViewerLoading = false;
        ElMessage.error('解析模型失败：' + error.message);
      }
    },

    renderFromArrayBuffer(fileName, arrayBuffer) {
      try {
        if (this.splatScene && viewer) {
          viewer.removeSplatScene(0);
        }

        const format = GaussianSplats3D.LoaderUtils.sceneFormatFromPath(fileName);
        
        this.fileBufferToSplatBuffer(arrayBuffer, format)
          .then(splatBuffer => {
            viewer.addSplatBuffers(
              [splatBuffer], 
              [{ splatAlphaRemovalThreshold: 1 }]
            ).then(() => {
              this.splatScene = true;
              this.isViewerLoading = false;
              viewer.start();
              ElMessage.success(`模型${fileName}加载成功！`);
            });
          })
          .catch(error => {
            this.isViewerLoading = false;
            ElMessage.error('解析模型失败：' + error.message);
          });
      } catch (error) {
        this.isViewerLoading = false;
        ElMessage.error('解析模型失败：' + error.message);
      }
    },

    fileBufferToSplatBuffer(buffer, format) {
      return new Promise((resolve, reject) => {
        try {
          let splatBufferPromise;
          
          if (format === GaussianSplats3D.SceneFormat.KSplat) {
            splatBufferPromise = GaussianSplats3D.KSplatLoader.loadFromFileData(buffer);
          } else if (format === GaussianSplats3D.SceneFormat.Ply) {
            splatBufferPromise = GaussianSplats3D.PlyLoader.loadFromFileData(buffer, 1, 0, true, 0);
          } else if (format === GaussianSplats3D.SceneFormat.Splat) {
            splatBufferPromise = GaussianSplats3D.SplatLoader.loadFromFileData(buffer, 1, 0, true);
          } else {
            reject(new Error('不支持的文件格式：' + format));
            return;
          }
          
          splatBufferPromise.then(resolve).catch(reject);
        } catch (err) {
          reject(new Error('格式转换失败：' + err.message));
        }
      });
    },

    receiveFileData(fileName, base64Data) {
      if (window.FlutterFileChannel) {
        this.renderFromBase64(fileName, base64Data);
      }
    },

    receiveFileError(errorMsg) {
      this.isViewerLoading = false;
      ElMessage.error(errorMsg);
    },

    triggerKeyAction(keyCode) {
      if (!this.splatScene || !viewer) return;

      const dispatchKeyEvent = (type) => {
        document.dispatchEvent(new KeyboardEvent(type, {
          code: keyCode,
          bubbles: true,
          cancelable: true
        }));
      };

      dispatchKeyEvent('keydown');
      setTimeout(() => dispatchKeyEvent('keyup'), 50);
    },

    toggleInfoPanel() {
      this.viewerControls.showInfo = !this.viewerControls.showInfo
      this.triggerKeyAction('KeyI');
    },
    toggleMeshCursor() {
      this.viewerControls.showMeshCursor = !this.viewerControls.showMeshCursor
      this.triggerKeyAction('KeyC');
    },
    toggleControlPlane() {
      this.viewerControls.showControlPlane = !this.viewerControls.showControlPlane
      this.triggerKeyAction('KeyU');
    },
    togglePointCloudMode(){
      this.viewerControls.isPointCloudMode = !this.viewerControls.isPointCloudMode;
      this.triggerKeyAction('KeyP');
    },
    adjustFocalUp() {
      this.triggerKeyAction('KeyG');
    },
    adjustFocalDown() {
      this.triggerKeyAction('KeyF');
    },
    toggleCameraProjectionMode() {
      this.viewerControls.isOrthographicCamera = !this.viewerControls.isOrthographicCamera
      this.triggerKeyAction('KeyO');
    },
    increaseSplatScale() {
      this.triggerKeyAction('Equal');
    },
    decreaseSplatScale() {
      this.triggerKeyAction('Minus');
    },
    upCamera() {
      this.triggerKeyAction('ArrowRight');
    },
    downCamera() {
      this.triggerKeyAction('ArrowLeft');
    },

    // 相机按钮点击处理
    handleCameraClick(dir) {
      if (this.viewerControls.usingExternalCamera) return;
      dir === 'up' ? this.upCamera() : this.downCamera();
      this.triggerGlow(dir);
    },

    // 触发发光效果
    triggerGlow(dir) {
      if (this.glowTimer) clearTimeout(this.glowTimer);
      this.isGlowing[dir] = true;
      this.glowTimer = setTimeout(() => {
        this.isGlowing[dir] = false;
      }, 1000);
    },

    // 长按开始
    startLongPress(dir) {
      if (this.viewerControls.usingExternalCamera) return;
      this.currentPressDir = dir;
      this.longPressTimer = setTimeout(() => {
        this.continueLongPress();
      }, 300);
    },

    // 持续长按
    continueLongPress() {
      if (!this.currentPressDir) return;
      this.currentPressDir === 'up' ? this.upCamera() : this.downCamera();
      this.longPressTimer = setTimeout(() => {
        this.continueLongPress();
      }, 100);
    },

    // 结束长按
    stopLongPress() {
      if (this.longPressTimer) {
        clearTimeout(this.longPressTimer);
        this.longPressTimer = null;
      }
      this.currentPressDir = null;
    },

    toggleFullScreen() {
      const container = this.$refs.viewerContainer;
      if (!container) return;

      if (!document.fullscreenEnabled) {
        ElMessage.error("当前浏览器不支持全屏功能");
        return;
      }

      try {
        if (!document.fullscreenElement) {
          container.requestFullscreen().then(() => {
            viewer.init();
            ElMessage.info("已进入全屏模式（双击可退出）");
          });
        } else {
          document.exitFullscreen().then(() => {
            viewer.init();
            ElMessage.info("已退出全屏模式（双击可进入）");
          });
        }
      } catch (err) {
        ElMessage.error(`全屏操作失败：${err.message}`);
        console.error("全屏切换错误：", err);
      }
    },

    handleTouchStart(e) {
      if (e.touches.length !== 1) {
        this.touchCount = 0;
        clearTimeout(this.touchTimeout);
        return;
      }

      e.preventDefault();
      const now = Date.now();
      const touchDelay = 300;

      if (this.touchCount === 0) {
        this.touchStartTime = now;
        this.touchCount = 1;

        this.touchTimeout = setTimeout(() => {
          this.touchCount = 0;
        }, touchDelay);
      } else if (this.touchCount === 1 && now - this.touchStartTime <= touchDelay) {
        clearTimeout(this.touchTimeout);
        this.isManualFullscreen = !this.isManualFullscreen;
        this.touchCount = 0;
      }
    },

    async destroyPreViewer() {
      if (viewer && this.splatScene) {
        try {
          viewer.removeSplatScene(0);
          this.splatScene = false;
          await viewer.dispose();
          viewer = null;
        } catch (error) {
          console.error('销毁 viewer 失败：', error);
        }
      }
    },

    toggleControlPanel() {
      if(!this.splatScene) {
        ElMessage.warning("请先导入模型！"); 
        return;
      }
      this.isControlPanelOpen = !this.isControlPanelOpen;
    },

    closeControlPanel() {
      if (this.isControlPanelOpen) {
        this.isControlPanelOpen = false;
      }
    },

    startDrag(e) {
      this.isDragging = true;
      if (e.type === 'mousedown') {
        this.startX = e.clientX;
        this.startY = e.clientY;
      } else {
        this.startX = e.touches[0].clientX;
        this.startY = e.touches[0].clientY;
      }
      this.totalDragX = 0;
      this.totalDragY = 0;
      const floatBtn = document.querySelector('.control-float-btn');
      floatBtn.classList.add('pressed');
    },

    handleDrag(e) {
      if (!this.isDragging) return;
      e.preventDefault();

      let clientX, clientY;
      if (e.type === 'mousemove') {
        clientX = e.clientX;
        clientY = e.clientY;
      } else {
        clientX = e.touches[0].clientX;
        clientY = e.touches[0].clientY;
      }

      const dx = clientX - this.startX;
      const dy = clientY - this.startY;
      const newLeft = this.floatLeft + dx;
      const newTop = this.floatTop + dy;
      const screenWidth = window.innerWidth;
      const screenHeight = window.innerHeight;
      const floatSize = 54;

      this.floatLeft = Math.max(
        this.screenPadding,
        Math.min(newLeft, screenWidth - floatSize - this.screenPadding)
      );
      this.floatTop = Math.max(
        this.screenPadding,
        Math.min(newTop, screenHeight - floatSize - this.screenPadding)
      );

      this.startX = clientX;
      this.startY = clientY;
    },

    stopDrag() {
      const floatBtn = document.querySelector('.control-float-btn');
      floatBtn.classList.remove('pressed');
      this.isDragging = false;
    },
  },
  mounted() {
    this.initViewer();
    window.receiveFileError = this.receiveFileError;
    window.receiveFileData = this.receiveFileData;

    const screenWidth = window.innerWidth;
    const screenHeight = window.innerHeight;
    const floatSize = 54;
    this.floatLeft = screenWidth - floatSize - this.screenPadding;
    this.floatTop = screenHeight / 2 - floatSize / 2;

    window.scrollTo({ top: 0, behavior: 'smooth' });
    
    if(this.isView && viewer) {
      this.renderFromArrayBuffer(this.viewFileName, this.viewData);
    }
  },
  beforeUnmount() {
    window.receiveFileData = null;
    window.receiveFileError = null;
    this.destroyPreViewer();
    if (this.longPressTimer) clearTimeout(this.longPressTimer);
    if (this.glowTimer) clearTimeout(this.glowTimer);

    const container = this.$refs.viewerContainer;
    if (container) {
      while (container.firstChild) {
        container.removeChild(container.firstChild);
      }
    }
  }
};
</script>

<style scoped>
/* 基础样式 */
.render-task-container {
  height: 80vh;
  background-color: #121212;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;
}

/* 导航栏 */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 20px;
  background-color: rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  position: relative;
  z-index: 10;
}

.nav-btn:hover {
  background-color: rgba(255, 255, 255, 0.2);
  color: #ffffff;
}

.nav-title {
  font-size: 18px;
  font-weight: 600;
  color: #ffffff;
  margin: 0;
  flex: 1;
  text-align: center;
  padding-left: 16px;
}

/* 核心渲染区 */
.core-content {
  flex: 1;
  position: relative;
  overflow: hidden;
}

.viewer-container {
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, #1e1e1e 0%, #0a0a0a 100%);
  cursor: grab;
  position: relative;
}

.viewer-container:active {
  cursor: grabbing;
}

.viewer-container.fullscreen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw !important;
  height: 100vh !important;
  z-index: 100;
  padding-top: constant(safe-area-inset-top);
  padding-top: env(safe-area-inset-top);
  padding-bottom: constant(safe-area-inset-bottom);
  padding-bottom: env(safe-area-inset-bottom);
}

/* 加载状态 */
.loading-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba(18, 18, 18, 0.7);
  z-index: 10;
}

.loading-spinner {
  width: 48px;
  height: 48px;
  border: 4px solid rgba(255, 255, 255, 0.1);
  border-left-color: #409eff;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 16px;
}

.loading-text {
  color: #ffffff;
  font-size: 16px;
  font-weight: 500;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

/* 空状态 */
