<template>
  <div class="app-container">
    <!-- 顶部导航栏 -->
    <div class="app-header">
      <h1 class="header-title">3D模型处理</h1>
    </div>

    <!-- 主内容区 -->
    <div class="main-content">
      <div class="content-container">
        <UploadTask v-show="activeTab === 'UploadTask'" @task-processing-get-tasks="test"></UploadTask>
        <RenderTask 
          v-if="activeTab === 'RenderTask'" 
          :is-view="isView"
          :view-data="viewData"
          :view-file-name="viewFileName"
        ></RenderTask>
        <TaskProcessing 
          v-show="activeTab === 'TaskProcessing'" 
          ref="RenderTaskRef" 
          @view-result="viewResult"
        ></TaskProcessing>
        <PersonStatus v-show="activeTab === 'PersonStatus'"></PersonStatus>
      </div>
    </div>

    <!-- 底部标签栏 -->
    <div class="tab-bar">
      <div 
        class="tab-item" 
        :class="{ active: activeTab === 'UploadTask' }"
        @click="handleTabClick('UploadTask')"  
      >
        <div class="tab-icon-wrapper">
          <el-icon class="tab-icon"><Upload /></el-icon>
        </div>
        <span class="tab-text">模型处理</span>
      </div>
      <div 
        class="tab-item" 
        :class="{ active: activeTab === 'RenderTask' }"
        @click="handleTabClick('RenderTask')" 
      >
        <div class="tab-icon-wrapper">
          <el-icon class="tab-icon"><Picture /></el-icon>
        </div>
        <span class="tab-text">模型渲染</span>
      </div>
      <div 
        class="tab-item" 
        :class="{ active: activeTab === 'TaskProcessing' }"
        @click="handleTabClick('TaskProcessing')" 
      >
        <div class="tab-icon-wrapper">
          <el-icon class="tab-icon"><Management /></el-icon>
        </div>
        <span class="tab-text">任务管理</span>
      </div>
      <div 
        class="tab-item" 
        :class="{ active: activeTab === 'PersonStatus' }"
        @click="handleTabClick('PersonStatus')" 
      >
        <div class="tab-icon-wrapper">
          <el-icon class="tab-icon"><User /></el-icon>
        </div>
        <span class="tab-text">个人中心</span>
      </div>
    </div>
  </div>
</template>

<script>
import RenderTask from './RenderTask.vue';
import PersonStatus from './PersonStatus.vue';
import TaskProcessing from './TaskProcessing.vue';
import UploadTask from './UploadTask.vue';
import { Upload, Picture, Management, User } from '@element-plus/icons-vue';

export default {
  components: {
    RenderTask,
    PersonStatus,
    TaskProcessing,
    UploadTask,
    Upload,
    Picture,
    Management,
    User
  },
  data() {
    return {
      activeTab: 'UploadTask',
      isView: false,
      viewData: null,
      viewFileName: null,
      isManualTabChange: false
    }
  },
  methods: {
    viewResult(data, fileName) {
      this.isManualTabChange = false;
      this.isView = true;
      this.viewData = data;
      this.viewFileName = fileName;
      this.activeTab = "RenderTask";
    },
    test() {
      console.log("123")
      if(this.$refs.RenderTaskRef)
        this.$refs.RenderTaskRef.doTask()
    },
    scrollToTop() {
      document.querySelector(".content-container").scrollTo({
        top: 0,
        behavior: 'smooth'
      });
    },
    handleTabClick(tabName) {
      this.isManualTabChange = true;
      this.activeTab = tabName;
      this.scrollToTop();
    }
  },
  watch: {
    activeTab() {
      if (this.isManualTabChange) {
        this.isView = false;
        this.isManualTabChange = false;
      }
    }
  }
}
</script>

