// src/router/index.js
import { createRouter, createWebHashHistory } from 'vue-router';
// 导入标签页对应的组件
import RenderTask from '@/components/RenderTask.vue'; // 路径按实际项目调整
import TaskProcessing from '@/components/TaskProcessing.vue';
import PersonStatus from '@/components/PersonStatus.vue';
// import HelloWorld from '@/components/HelloWorld.vue';
const routes = [
  // 父路由：对应标签页容器页面
  { path: '/render-task', component: RenderTask }, // 模型处理：/tab/render-task
{ path: '/task-processing', component: TaskProcessing }, // 任务管理：/tab/task-processing
{ path: '/person-status', component: PersonStatus }, // 个人中心：/tab/person-status
  // 可选：默认重定向到第一个标签页
//   { path: '/', redirect: '/render-task' }
];

const router = createRouter({
  history: createWebHashHistory(), // 哈希模式（适合嵌入Flutter等场景，避免后端配置）
  routes
});

export default router;